export default {
    name:'Base',
    data(){
        return{
            phoneNumber:'',
        }
    },
    methods: {
        clickNumber(){
            alert('shuju');
            var url = 'http://apis.juhe.cn/mobile/get?phone='+this.phoneNumber+"&key=7ae71995d6f573bf27e46a48c4b7a535";
            this.axios.get(url)
            .then(res => {
                alert(res);
            })
            .catch(err => {
                alert(err);
            })
        }
    },
}



